import React, { Component, PropTypes } from 'react';

import styles from './SideMenu.styl';

import MenuItem from '../MenuItem';

export default class SideMenu extends Component {
  constructor() {
    super();
    this.state = {
      isShown: true,
    };
    this.toggleSideMenu = this.toggleSideMenu.bind(this);
  }

  toggleSideMenu() {
    this.setState({
      shown: !this.state.shown,
    });
  }

  render() {
    const { isShown } = this.props;
    const { SideMenuItems, className, children } = this.props;

    return (
      <div className={isShown ? styles.shown : styles.sideMenu}>
        {children}
        <div className={styles.content}>
          {SideMenuItems.map((item, i) => (
            <MenuItem className={styles.menuItem} key={i} item={item} />
          ))}
        </div>

      </div>
    );
  }
}

SideMenu.defaultProps = {
  isShown: false,
};

SideMenu.propTypes = {
  isShown: PropTypes.bool,
  SideMenuItems: PropTypes.arrayOf(React.PropTypes.object).isRequired,
};



